/********placeHolder********/
input,
textarea,
select,
ol,
ul,
li,
a,
p,
h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
  text-decoration: none;
  outline: none;
  list-style: none;
  padding: 0;
}

a,
a:hover,
a:focus,
*:focus-visible {
  cursor: pointer;
  transition: all 0.4s ease 0s;
  text-decoration: none;
  outline: none;
}

button:focus {
  outline: none;
  border: none;
}

button:focus,
input:focus {
  box-shadow: none !important;
}

button {
  cursor: pointer;
}

*,
*:after,
*:before {
  box-sizing: border-box;
}

section {
  overflow-x: hidden;
}

body {
  margin: 0;
  padding: 0;
  background: #fff;
  scroll-behavior: smooth;
  overflow-x: hidden;
}

.fit-img {
  width: 100%;
  height: 100%;
  -o-object-fit: cover;
  object-fit: cover;
}

.cheader .navbar-brand {
  display: block;
  width: 390px;
}

.hero_banner {
  margin: 54px 0px 36px calc((100% - 980px) * 0.5);
  margin-left: auto;
  margin-right: auto;
}

.hero_banner .hero_png {
  width: 390px;
}

.hero_banner h1 {
  font-size: 50px;
  letter-spacing: -1px;
  font-weight: 400;
  color: #cc0000;
  font-family: "Fraunces";
}

.hero_banner h2 {
  font-size: 80px;
  font-style: italic;
  font-weight: 400;
  color: #cc0000;
  font-family: "Fraunces";
}

.hero_banner h3 {
  font-size: 50px;
  font-weight: 400;
  color: #041e42;
  font-family: "Fraunces";
  line-height: 64.8px;
}

.hero_banner p {
  font-size: 32px;
  line-height: 44.8px;
  color: #041e42;
  font-family: "Avenir LT Std";
  letter-spacing: -0.32px;
  font-weight: 300;
}

.hero_banner .bn_top {
  padding: 0px 0px 35px calc((100% - 980px) * 0);
  border-bottom: 1px solid #000000;
}

.journey {
  padding-bottom: 39px;
}

.journey p {
  font-size: 16px;
  line-height: 24px;
  font-weight: 400;
  color: #041e42;
  font-family: "Avenir LT Std";
  font-style: italic;
}

.lglobal_btn {
  padding: 10px 40px;
  color: #fff;
  font-size: 25px;
  line-height: 35px;
  font-family: "Avenir LT Std";
  font-weight: 300;
  position: relative;
  z-index: 2;
  box-shadow: 0 1px 4px rgba(0, 0, 0, 0.6), 0 1px 4px rgba(0, 0, 0, 0.6);
}

.lglobal_btn:hover {
  background-color: #fff;
  color: #000;
}

.lglobal_btn::after {
  content: "";
  background: url("../public/assets/images/arrow-left.png") no-repeat;
  bottom: -27px;
  height: 26px;
  pointer-events: none;
  position: absolute;
  width: 165px;
  background-position: 100% 0;
  right: -20px;
  z-index: 0;
}

.lglobal_btn::before {
  content: "";
  background: url("../public/assets/images/arrow-right.png") no-repeat;
  bottom: -27px;
  height: 26px;
  pointer-events: none;
  position: absolute;
  width: 165px;
  background-position: 0 0;
  left: -20px;
  z-index: 0;
}

