.lsticky {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 333;
  background: white;
}

.anim-t {
  animation: stick 0.6s linear !important;
}

@keyframes stick {
  0% {
    opacity: 0%;
  }
  50% {
    opacity: 50%;
  }
  100% {
    opacity: 100%;
  }
}
.test_slider,
.pricing {
  background: #fafafa;
}
.test_slider_main .slick-arrow {
  position: absolute;
  z-index: 5;
  top: 50%;
  transform: translateY(-50%);
  color: black;
  border: none;
  opacity: 0.7;
  transition: all 0.3s ease-in-out;
}

.test_slider_main .slick-list {
  overflow: visible !important;
}
.test_slider_main .slick-prev {
  left: 0% !important;
  transform: translate(-47%, -50%);
}
.test_slider_main .slick-next {
  right: 0% !important;
  transform: translate(47%, -50%);
}
.test_slider_main .slick-prev:hover:before,
.test_slider_main .slick-prev:focus:before,
.test_slider_main .slick-next:hover:before,
.test_slider_main .slick-next:focus:before {
  display: none;
}
.test_slider_main .slick-arrow {
  width: 35px;
  height: 35px;
  color: #000;
}
.test_slider_main .slick-prev {
  background: url("../public/assets/images/left-chevron.png") no-repeat;
  opacity: 1;
  z-index: 4;
  background-size: contain;
}
.test_slider_main .slick-next {
  background: url("../public/assets/images/chevron.png") no-repeat;
  opacity: 1;
  z-index: 4;
  background-size: contain;
}
.test_slider_main .slick-prev:before {
  display: none;
}
.test_slider_main .slick-next:before {
  display: none;
}
.test_slider_main .slick-slider {
  position: unset !important;
}
.test_slider_main .slick-dots {
  left: 0px;
  bottom: 50px !important;
}

.test_slider_main .slick-dots li.slick-active button {
  color: #041e42;
  width: 8px;
  height: 8px;
  background-color: white;
  border-radius: 50px;
  border: 2px solid #041e42;
  font-size: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  top: 50%;
  transform: translateY(-50%);
  padding: 3px;
}

.test_slider_main .slick-dots li button {
  color: #041e42;
  width: 6px;
  height: 6px;
  background-color: #041e42;
  border-radius: 50px;
  font-size: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  top: 50%;
  transform: translateY(-50%);
  position: absolute;
  padding: 0;
}

.test_slider_main .slick-dots li button:before {
  display: none;
}
.test_slider_main .slick-dots {
  display: flex !important;
  align-items: center !important;
